
import XHeader from 'vux/src/components/x-header/index.vue'
import PopupHeader from 'vux/src/components/popup-header/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Alert from 'vux/src/components/alert/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'
import Box from 'vux/src/components/box/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Countdown from 'vux/src/components/countdown/index.vue'
import Masker from 'vux/src/components/masker/index.vue'

import {httpUrl} from '../../http_url';

export default {
name: 'HelloWorld',
data () {
return {
menus: {
menu1: '退出登录'
},
showMenus: false,
show: true,
time:60,
start: false,
oldPhone:'123456789',
phone: '',
identifyCode:'',
}
},
components: {
XHeader,PopupHeader,Actionsheet,Box,
Group,
XInput,
XButton, Alert,Countdown,Masker
},
created(){
let phone = this.$route.query.phone;
this.oldPhone = phone;

},
methods: {
getCode(){
console.log(this.SpaceTransformation(this.phone));
let postData = this.$qs.stringify({
phone:this.SpaceTransformation(this.phone),
type:102
})
this.$axios({
method: 'get',
url:httpUrl.getCode,
headers: {
'Content-Type': 'application/x-www-form-urlencoded;charset=UTF-8'
},
data:postData
}).then((res)=>{
console.log(res.data)
if(res.data.errCode==200){
this.$vux.toast.show({
text: '验证码发送成功'
});
this.show = false;
this.start = true;
}else if(res.data.errCode==201){
this.$vux.toast.show({
type:'warn',
text: res.data.retMsg
})
this.phone = '';
}
}).catch(function () {
this.$vux.toast.show({
text: '网络异常，请稍后再试',
type:'warn'
})
});
},
finish (index) {
this.start = false;
this.time = 60;
this.show = true;
},
submit(){
const _this = this;
let postData = this.$qs.stringify({
phone:this.SpaceTransformation(this.phone),
code:this.identifyCode,
})
this.$axios({
method: 'post',
url:httpUrl.changePhone,
data:postData
}).then((res)=>{
console.log(res.data)
if(res.data.errCode==200){
this.$vux.alert.show({
title: '提示',
content: '手机号码修改成功',
onHide () {
_this.LoginOut();
}
});
}else if(res.data.errCode==201){
this.$vux.toast.show({
type:'warn',
text: res.data.retMsg
})
}else if(res.data.errCode==401){
this.$vux.alert.show({
title: '错误提示',
content: '未登录，请先登录',
onHide () {
_this.$router.push('/quick_login');
}
})
}
}).catch(function () {
this.$vux.toast.show({
text: '网络异常，请稍后再试',
type:'warn'
})
});
},
goHome(){
this.$router.push('/home');
},
LoginOut(){
this.$axios({
method: 'post',
url:httpUrl.loginOut,
headers: {
'Content-Type': 'application/x-www-form-urlencoded;charset=UTF-8'
},
}).then((res)=>{
console.log(res.data)
if(res.data.errCode==200){
this.$router.push('/quick_login');
}else if(res.data.errCode==201){
this.$vux.toast.show({
type:'warn',
text: res.data.retMsg
})
}

}).catch(function () {
this.$vux.toast.show({
text: '网络异常，请稍后再试',
type:'warn'
})
});
},
},
filters: {

}
}



